.sample {
  background-color: #eaeaea;

  @media (min-width: $desktop-width) {
    display: flex;
  }
}

.sample__title {
  padding-top: 25px;
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    margin-bottom: 60px;
  }
}

.sample__content p {
  font-size: 14px;
  line-height: 18px;
  color: #444444;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 24px;
  }
}

.sample__content {
  @media (min-width: $tablet-width) {
    background-color: #ffffff;
  }

  @media (min-width: $desktop-width) {
    background-color: #f2f2f2;
  }
}

.sample__item {
  border: 1px solid #dcdcdc;
  border-radius: 3px;
  margin-bottom: 10px;

  @media (min-width: $tablet-width) {
    margin-right: 42px;
  }
}

.sample__number {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 37px;
}

.sample__description {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
  margin-bottom: 40px;
}

.sample__range {
  padding-top: 15px;
  padding-bottom: 40px;
  position: relative;
  text-align: center;
}


.sample__range-toggle {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  border: none;
  background-color: transparent;
  padding: 5px;
}

.sample__range-toggle:first-child {
  padding-right: 100px;
}

.sample__range-toggle:first-child::before {
  position: absolute;
  width: 85px;
  height: 14px;
  top: 23px;
  left: 95px;
  background-color: #ffffff;
  border: 1px solid #cdcdcd;
  border-radius: 6px;
  content: "";
}


.sample-legend {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
  }
}

.sample__list {
  @media (min-width: $tablet-width) {
    display: flex;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
  }
}

.sample__slider-container {
  @media (min-width: $desktop-width) {
    background-color: #f2f2f2;
  }
}
